.multi-select-dropdown {
    position: relative;
    width: 300px;
    font-family: Arial, sans-serif;
    font-size: 14px;
  }
  
  .dropdown-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    cursor: pointer;
  }
  
  .dropdown-placeholder {
    color: #333;
    flex: 1;
  }
  
  .dropdown-arrow {
    font-size: 10px;
    color: #333;
  }
  
  .dropdown-options {
    position: absolute;
    z-index: 10;
    width: 100%;
    margin: 5px 0 0;
    padding: 0;
    list-style: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    max-height: 200px;
    overflow-y: auto;
  }
  
  .dropdown-option {
    display: flex;
    align-items: center;
    padding: 8px 10px;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  .dropdown-option:hover,
  .dropdown-option.highlighted {
    background-color: #f0f0f0;
  }
  
  .dropdown-option.selected {
    font-weight: bold;
  }
  
  .checkbox-container {
    display: flex;
    align-items: center;
    margin-right: 10px;
    width: 20px;
  }
  
  .checkbox-container input {
    display: none;
  }
  
  .checkmark {
    display: inline-block;
    width: 18px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    font-size: 12px;
    background-color: #007bff;
    color: white;
    border-radius: 3px;
    visibility: hidden;
  }
  
  .checkbox-container input:checked + .checkmark {
    visibility: visible;
  }
  